export default {
  props: {
  isAdmin: {
    type: Boolean,
    default: false,
    required: false
  },
  items: {
    type: Array,
    required: true
  }},
  data: () => ({
    loading: false,
    selection: 1,
  }),
  

  methods: {
    reserve () {
      this.loading = true

      setTimeout(() => (this.loading = false), 2000)
    },
    view(id){
      if(this.isAdmin){
        this.$router.push('admin/'+ id)
      }else{
        this.$router.push('/items/' + id)
      }
      
    },
    borrow(id){
       if(this.isAdmin){
        this.$store.dispatch('DELETE_ITEM', id).then(() => {
      this.$router.push("/");

    });
      }else{
        this.$router.push('/borrow')
      }
    }
  },
}
